<template>
	<view class="wall-wrap">
		<view class="art-item" v-for="(item, index) in list" :key="index">
			<view class="content" @click="handleCopy(item)">{{ item.content }}</view>
			<view class="act-btn" @click="handleCopy(item)">
				<image src="../../static/copy.png" mode="aspectFit"></image>
				<text class="copy">复制</text>
				<text>{{ item.copyNum }}</text>
			</view>
		</view>
		<uni-load-more :status="status" :content-text="contentText"></uni-load-more>
	</view>
</template>
<script>
export default {
	data() {
		return {
			status: 'more',
			initLoad: false,
			from: {
				page: 1,
				pageSize: 10
			},
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '加载中...',
				contentnomore: '我是有底线的'
			},
			list: [],
			resList: []
		};
	},
	async onShow() {
		this.initLoad = true;
		this.list = [];
		setTimeout(function() {
			console.log('start pulldown');
		}, 1000);
		uni.startPullDownRefresh();
	},
	onReachBottom() {
		console.log('onReachBottom');
		this.initLoad = false;
		if (this.resList.length < 10) {
			this.status = 'noMore';
			return;
		}
		this.status = 'loading';
		setTimeout(() => {
			if (this.resList.length === 10) {
				this.from.page++;
			}
			this.getData();
		}, 300);
	},
	async onPullDownRefresh() {
		this.from.page = 1;
		this.getData();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 300);
	},
	methods: {
		// 获取数据
		async getData() {
			const {
				code,
				result: { data }
			} = await this.$api.wall.fetchList(this.from);
			if (code === 200) {
				this.resList = data;
				let formatList = [];

				if (this.list.length === 0) {
					this.list = data;
				} else {
					this.list = this.list.map(item => {
						return data.find(i => i._id === item._id) || item;
					});
					let ids = this.list.map(item => item._id);
					formatList = data.filter(item => !ids.includes(item._id));
				}
				// 第一次加载新数据加在前面
				if (this.initLoad) {
					this.list = (this.list.length === 0 ? data : formatList).concat(this.list);
				} else {
					this.list = this.list.concat(this.list.length === 0 ? data : formatList);
				}
				if (data.length === 0) {
					this.status = 'noMore';
				}
			}
		},
		// 复制数
		async handleCopy(row) {
			const { content, _id } = row;
			let obj = {
				...row,
				copyNum: row.copyNum++
			};
			const { code, message } = await this.$api.wall.editWall(obj);
			if (code === 200) {
				uni.setClipboardData({
					data: content,
					success: function() {
						//调用方法成功
						console.log('success');
					}
				});
			}
		}
	}
};
</script>
<style lang="scss" scoped>
.wall-wrap {
	background: #f5f5f5;
	padding: 20rpx 15rpx;
	.art-item {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		flex-direction: column;
		background-color: #fff;
		border-radius: 20rpx;
		box-shadow: 0rpx 0rpx 5rpx 1rpx rgba(0, 0, 0, 0.1);
		margin-bottom: 20rpx;
		overflow: hidden;
		.content {
			padding: 40rpx 40rpx;
			font-size: 30rpx;
			line-height: 1.5;
			color: #333333;
		}
		.act-btn {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			box-sizing: border-box;
			font-size: 24rpx;
			background-color: #f9f9f9;
			padding: 15rpx 30rpx;
			color: #888888;
			font-weight: 500;
			font-weight: bold;
			image {
				width: 30rpx;
				height: 30rpx;
			}
			.copy {
				margin: 0 10rpx;
			}
		}
	}
}
</style>
